import React, { useState, useEffect, useCallback, Suspense } from "react"
import { Layout, Section, Card, Heading, ButtonLink, Tooltip, Loading, Alert, Button } from "components"
import { Row, Col, List, Icon, Typography, Affix, message } from "antd"
import styled from "styled-components"
import moment from "moment"
import { useMutation, useQuery } from "react-apollo"
import {
	mutateUserProfile,
	mutateFetchTestimonials,
	queryReferralLink,
	MUTATE_USER_BY_TOKEN,
	MUTATE_TESTIMONIALS_BY_TOKEN
} from "queries/profile"
import { withRouter, Link } from "react-router-dom"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import Helmet from "react-helmet"
import { gapi } from "gapi-script"

import { mobile, newClient, client } from "helpers"
import { dummyTestimonials } from "dummy"
import referralMail from "assets/images/referral_mail.png"
import whoViewed from "assets/images/who-viewed.png"
import useQueryData from "helpers/hooks/useQueryData"
import { country, AppContext } from "App"
import { countryMap } from "helpers/countryMapping"
import { ALL, UPDATE } from "queries/booking"
import { CUDY_URL } from "helpers/constants"

const ProfileCover = React.lazy(() => import("./ProfileCover"))
const IntroVideos = React.lazy(() => import("./IntroVideos"))
const Subjects = React.lazy(() => import("./Subjects"))
const AboutMe = React.lazy(() => import("./AboutMe"))
const Skills = React.lazy(() => import("./Skills"))
const OtherPlatforms = React.lazy(() => import("./OtherPlatforms"))
const Certifications = React.lazy(() => import("./Certifications"))
const ShareModal = React.lazy(() => import("./ShareModal"))
const AvatarCard = React.lazy(() => import("./AvatarCard"))
const Experiences = React.lazy(() => import("./Experiences"))
const TutorCalendar = React.lazy(() => import("./Calendar"))
const Chat = React.lazy(() => import("../../../components/common/Chat"))
const Education = React.lazy(() => import("./Education"))
const RenderTestimonials = React.lazy(() => import("./_RenderTestimonials"))
const CompleteProfileModal = React.lazy(() => import("./_CompleteProfileModal"))

const CardTitle = ({ title, id, description = "", onEditMode, noEdit, unauthorizedUser }) => (
	<StyledCardTitle className="card-title">
		<Heading content={title} subheader={description} level={4} />
		{unauthorizedUser || noEdit ? null : (
			<span className="icon" onClick={() => onEditMode(id)}>
				<Icon type="edit" />
			</span>
		)}
	</StyledCardTitle>
)

function TutorProfile({ match, history, peopleProfile, i18n }) {
	const [teachingDate, setTeachingDate] = useState([])
	const [userData, setUserData] = useState({})
	const [editMode, setEditMode] = useState("")
	const [subjects, setSubjects] = useState([])
	const [nonAcademicSubjects, setNonAcademicSubjects] = useState([])
	const [userBio, setUserBio] = useState({})
	const [skills, setSkills] = useState([])
	const [userOtherPlat, setUserOtherPlat] = useState([])
	const [certifications, setCertifications] = useState([])
	const [experiences, setExperiences] = useState([])
	const [experiencesNonAcademic, setExperiencesNonAcademic] = useState([])
	const [testimonials, setTestimonials] = useState([])
	const [introVideos, setIntroVideos] = useState([])
	const [education, setEducation] = useState([])
	const [shareModal, setShareModal] = useState(false)
	const [profileCompletionModal, setProfileCompletionModal] = useState(false)
	const [chatWindow, setChatWindow] = useState(false)
	const [unauthorizedUser, setUnauthorizedUser] = useState(false)
	const [profileBanner, setProfileBanner] = useState({ file: "", imagePreview: "", actual: "" })
	const [localFile, setLocalFile] = useState({
		file: "",
		imagePreview: "",
		isAvatar: false,
		actual: ""
	})
	const [pictureSettings, setPictureSettings] = useState({
		crop: { x: 0, y: 0 },
		zoom: 1,
		aspect: 3 / 3
	})

	const {
		onMarketplace: { valueFromMarketplace }
	} = React.useContext(AppContext)

	// const fromMarketplace = onMarketplace
	const token = localStorage.getItem("token")
	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	const completionAlertDismissed = localStorage.getItem("completion_alert_dismissed")
	const roleCode = (userDetails.role || {}).code
	const userParams = match.params || {}

	const getTeachingDate = (dates) => {
		if (dates) {
			return JSON.parse(dates).map((d) => ({
				...d,
				// title: d.id.match(/^booking/) ? d.title : "Busy",
				// title: unauthorizedUser ? "Busy" : d.title,
				title: "Blocked time",
				location: unauthorizedUser ? "-" : d.location
			}))
		}
		return []
	}

	const { data: booking, refetch: refetchBooking } = useQuery(ALL, {
		skip: roleCode != "TTR0" || userParams.token,
		fetchPolicy: "network-only",
		client: client["bookingClient"],
		variables: { role: "tutor", status: "paid" }
	})

	const [updateBooking] = useMutation(UPDATE, {
		client: newClient("booking"),
		onCompleted: () => {
			refetchBooking()
		},
		onError: (error) => {
			message.error(i18n._(t`${error}`) + countryMap[country].name, 3)
		}
	})

	const [fetchUser, { loading }] = useMutation(mutateUserProfile, {
		client: newClient(),
		onCompleted: (data) => {
			data = data.getProfile || {}
			setUserData(data)
			const avatar = data.avatar.length > 1 ? data.avatar[data.avatar.length - 1] : data.avatar[0]
			const userBio = {
				education: data.educationList,
				location: data.address,
				description: data.description
			}
			setSubjects(data.subjectList)
			setNonAcademicSubjects(data.nonAcademic)
			setUserBio(userBio)
			setExperiences(data.experienceList)
			setExperiencesNonAcademic(data.experienceNonAcademicList)
			setSkills(data.skillList)
			setCertifications(data.certificateList)
			setIntroVideos(data.videoIntroductionList)
			setUserOtherPlat(data.otherPlatformList)
			setEducation(data.educationList)
			setProfileBanner((prev) => ({ ...prev, actual: (data.banner[0] || {}).path }))
			setLocalFile((prev) => ({ ...prev, actual: (avatar || {}).path }))
			setTeachingDate(getTeachingDate(data.eventsFromGoogle))
		},
		onError: (err) => {
			const error = (err.graphQLErrors || [])[0] || {}
			if (error.statusCode === 401) {
				setUnauthorizedUser(true)
			}
		}
	})

	const [fetchUserByToken, { data: dataByToken = {}, loading: loadingByToken }] = useMutation(MUTATE_USER_BY_TOKEN, {
		client: newClient(),
		onCompleted: (data) => {
			data = data.getProfile || {}
			setUserData(data)
			const avatar = data.avatar.length > 1 ? data.avatar[data.avatar.length - 1] : data.avatar[0]
			const userBio = {
				education: data.educationList,
				location: data.address,
				description: data.description
			}
			setSubjects(data.subjectList)
			setNonAcademicSubjects(data.nonAcademic)
			setUserBio(userBio)
			setExperiences(data.experienceList)
			setExperiencesNonAcademic(data.experienceNonAcademicList)
			setSkills(data.skillList)
			setCertifications(data.certificateList)
			setIntroVideos(data.videoIntroductionList)
			setUserOtherPlat(data.otherPlatformList)
			setEducation(data.educationList)
			setProfileBanner((prev) => ({ ...prev, actual: (data.banner[0] || {}).path }))
			setLocalFile((prev) => ({ ...prev, actual: (avatar || {}).path }))
			setTeachingDate(getTeachingDate(data.eventsFromGoogle))
		},
		onError: (err) => {
			const error = (err.graphQLErrors || [])[0] || {}
			if (error.statusCode === 401) {
				setUnauthorizedUser(true)
			}
		}
	})

	const [fetchTestimonials] = useMutation(mutateFetchTestimonials, {
		client: newClient(),
		onCompleted: (data = {}) => {
			data = data.getTestimonialList || []
			setTestimonials(data)
		}
	})

	const [fetchTestiByToken] = useMutation(MUTATE_TESTIMONIALS_BY_TOKEN, {
		client: newClient(),
		onCompleted: (data = {}) => {
			data = data.getTestimonialListByToken || []
			setTestimonials(data)
		}
	})

	const handleUpdateBooking = async (item, status) => {
		try {
			if (status === "accept") {
				await gapi.client.calendar.events.insert({
					calendarId: "primary",
					resource: {
						id: `booking${item.pvid}`,
						summary: `${item.subjectInfo.subject} - ${item.subjectInfo.level}`,
						description: "cudy",
						location: item.location || <Trans>No Location</Trans>,
						start: { dateTime: item.startTime },
						end: { dateTime: item.endTime },
						attendees: [{ email: item.student.email }],
						reminders: {
							useDefault: false,
							overrides: [
								{ method: "email", minutes: 3 * 60 },
								{ method: "popup", minutes: 10 }
							]
						}
					}
				})
			}
			await updateBooking({ variables: { id: Number(item.pvid), status } })
		} catch (error) {
			message.error(i18n._(t`${error}`))
		}
	}

	const {
		data: { referralLink }
	} = useQueryData(queryReferralLink, "profileClient")
	const completionNotSufficient = userData.completionUser && userData.completionUser.completionPercent < 80
	const handleEditMode = (id) => setEditMode(id)

	const handleUploadImage = (e) => {
		e.preventDefault()

		const reader = new FileReader()
		const file = e.target.files[0]

		const isAvatar = localFile.isAvatar
		const handlers = isAvatar ? setLocalFile : setProfileBanner

		reader.onloadend = () => {
			handlers((prev) => ({ ...prev, file, imagePreview: reader.result }))
		}

		reader.readAsDataURL(file)
	}

	const experienceTotal = experiences.map((item) => {
		const fromYear = (item.subject?.level[0].fromDate || "").split("-")[0]
		const fromMonth = (item.subject?.level[0].fromDate || "").split("-")[1]
		const toYear = (item.subject?.level[0].toDate || "").split("-")[0]
		const toMonth = (item.subject?.level[0].toDate || "").split("-")[1]

		let startTime = moment(`${Number(fromYear)} ${Number(fromMonth)}`)
		let endTime = moment(`${Number(toYear)} ${Number(toMonth)}`)

		let totalSec = endTime.diff(startTime, "months")

		return totalSec
	})

	const theTotal = (experienceTotal.reduce((acc, curr) => acc + curr, 0) / 12).toFixed(1)
	const experiencesDescription = theTotal === "0.0" ? <Trans>No experience yet</Trans> : <Trans>{parseFloat(theTotal)}y exp</Trans>

	const tutorTestimonials = (token ? testimonials : dummyTestimonials).map(({ reviewer = {}, ...item }) => ({
		title: reviewer.firstName + " " + reviewer.lastName,
		description: item.comment,
		extra: item.postedDate
	}))

	const handleUserStatus = useCallback(() => {
		if (peopleProfile || userParams.token) {
			const { keywords = "", subjects, curriculums } = valueFromMarketplace
			setUnauthorizedUser(true)
			fetchUserByToken({
				variables: {
					keywords,
					code: userParams.token || 0,
					subjects: !subjects || subjects === 0 ? [] : [String(subjects)],
					curriculums: !curriculums || curriculums === 0 ? [] : [String(curriculums)]
				}
			})
			fetchTestiByToken({ variables: { token: userParams.token || "", limit: 10 } })
		} else {
			setUnauthorizedUser(false)
			fetchUser()
			fetchTestimonials({ variables: { limit: 10 } })
			history.push(`/${country}/profile/me`)
		}
	}, [fetchTestiByToken, fetchTestimonials, fetchUser, fetchUserByToken, history, peopleProfile, userParams.token, valueFromMarketplace])

	const userDataCountry = (dataByToken.getProfile || {}).country

	useEffect(() => {
		if ((!peopleProfile && !token) || (!token && roleCode === "STDN")) {
			history.push(`/${country}/404`)
			return
		}
		handleUserStatus()
	}, [handleUserStatus, history, peopleProfile, roleCode, token, userParams.token])

	if (peopleProfile && userDataCountry && userDataCountry !== country) {
		history.push("/")
		message.error(i18n._(t`You are being redirected as the Tutor doesn't teach in `) + countryMap[country].name, 3)
	}

	return (
		<Layout customBread={peopleProfile && userData.firstName + " " + userData.initialName}>
			<Helmet>
				<meta name="title" content={userData.firstName + userData.lastName} />
				<meta name="description" content="Description from Profile Tutor" />
				<meta property="og:title" content={userData.firstName + userData.lastName} />
				<meta property="og:description" content="Description from Profile Tutor" />
			</Helmet>

			<Suspense fallback={<Loading />}>
				{!unauthorizedUser && completionNotSufficient && !completionAlertDismissed && (
					<Affix>
						<Alert
							className="mb0"
							message={
								<Trans>
									You only can be found after completing at least <strong>60%</strong> of your profile. Come complete and
									make your profile easier to find!
								</Trans>
							}
							banner
							closable
							afterClose={() => localStorage.setItem("completion_alert_dismissed", true)}
						/>
					</Affix>
				)}
				<CompleteProfileModal onModal={{ profileCompletionModal, setProfileCompletionModal }} userData={userData} />
				<ShareModal userData={userData} onModal={{ shareModal, setShareModal }} />

				{chatWindow && <Chat onChatWindow={{ chatWindow, setChatWindow }} />}

				<Row>
					<Col lg={24}>
						<ProfileCover
							onChangeCover={handleUploadImage}
							unauthorizedUser={unauthorizedUser}
							onFile={{ profileBanner, setProfileBanner }}
							fetchUser={fetchUser}
							loading={loadingByToken || loading}
						/>
					</Col>
				</Row>

				<Section paddingHorizontal="very" maxWidth="1400px" centered style={{ position: "relative", top: "-80px" }}>
					<Row gutter={32}>
						<Col lg={6} order={mobile ? 2 : 1}>
							<AvatarCard
								data={{ userData, theTotal, userDetails, teachingDate }}
								unauthorizedUser={unauthorizedUser}
								onUploadImage={handleUploadImage}
								onShareModal={{ shareModal, setShareModal }}
								onLocalFile={{ localFile, setLocalFile }}
								onSettings={{ pictureSettings, setPictureSettings }}
								fetchUser={handleUserStatus}
								onChatWindow={{ chatWindow, setChatWindow }}
								loading={loadingByToken || loading}
							/>

							{!unauthorizedUser && (
								<MainCard title={<CardTitle noEdit />}>
									<Row gutter={16} type="flex" justify="center" align="middle" className="mb1em">
										<Col lg={12}>
											<img src={whoViewed} alt="Who's viewed me?" width="100%" />
										</Col>
									</Row>
									<Row type="flex" justify="center">
										<Col lg={24}>
											<Link to={`/${country}/profile/viewed`}>
												<ButtonLink type="default">
													<Trans>Who's viewed me?</Trans>
												</ButtonLink>
											</Link>
										</Col>
									</Row>
								</MainCard>
							)}

							{!unauthorizedUser && (
								<MainCard>
									{loadingByToken || loading ? (
										<Loading />
									) : (
										<>
											<img
												src={referralMail}
												width="100%"
												alt={<Trans>Refer your friend to join Cudy</Trans>}
												className="mb2em"
											/>
											<Heading
												content={<Trans>Refer it!</Trans>}
												subheader={
													<Trans>
														You can refer your profile to your friend and get some extra benefits. Copy and
														share your referral link below to get started
													</Trans>
												}
												level={4}
											/>
											<Typography.Paragraph strong copyable={{ text: referralLink }}>
												<Tooltip title={referralLink}>
													<Trans>Copy link</Trans>
												</Tooltip>
											</Typography.Paragraph>
										</>
									)}
								</MainCard>
							)}

							{!unauthorizedUser && (
								<MainCard id="testimoniallink" title={<CardTitle title={<Trans>Testimonials link</Trans>} noEdit />}>
									<p className="mb1em">
										<Trans>
											Get your past students to give you a testimonial. Copy the link and share to them. Let them
											speak!{" "}
											<span role="img" aria-label="Students">
												🙋🏼‍♂️
											</span>
										</Trans>
									</p>
									<Alert
										type="info"
										showIcon
										message={
											<Trans>
												Your students will receive an email for verification to activate your testimonial once
												submitted
											</Trans>
										}
									/>
									<Typography.Paragraph
										strong
										copyable={{
											text: `${CUDY_URL}/${userData.country}/profile/add_testimonials?referral_code=${userData.referralCode}`
										}}
									>
										<Tooltip
											title={`${CUDY_URL}/${userData.country}/profile/add_testimonials?referral_code=${userData.referralCode}`}
										>
											<Trans>Copy link</Trans>
										</Tooltip>
									</Typography.Paragraph>
								</MainCard>
							)}

							<MainCard
								title={
									<CardTitle
										id="skills"
										title={<Trans>Skills</Trans>}
										onEditMode={handleEditMode}
										unauthorizedUser={unauthorizedUser}
									/>
								}
							>
								<Skills
									onEditMode={{ editMode, setEditMode }}
									onSkills={{ skills, setSkills }}
									fetchUser={fetchUser}
									loadingSkills={loadingByToken || loading}
									unauthorizedUser={unauthorizedUser}
								/>
							</MainCard>

							<MainCard
								title={
									<CardTitle
										id="otherplatforms"
										title={<Trans>Other platforms</Trans>}
										onEditMode={handleEditMode}
										unauthorizedUser={unauthorizedUser}
									/>
								}
							>
								<OtherPlatforms
									onEditMode={{ editMode, setEditMode }}
									onOtherPlat={{ userOtherPlat, setUserOtherPlat }}
									unauthorizedUser={unauthorizedUser}
									fetchUser={fetchUser}
									loading={loadingByToken || loading}
								/>
							</MainCard>
						</Col>

						<Col lg={18} order={mobile ? 1 : 2}>
							<MainCard
								bg="floralwhite"
								title={
									<CardTitle
										id="introvideos"
										noEdit
										title={<Trans>Introduction videos</Trans>}
										onEditMode={handleEditMode}
										unauthorizedUser={unauthorizedUser}
									/>
								}
							>
								<IntroVideos
									data={introVideos}
									fetchUser={fetchUser}
									unauthorizedUser={unauthorizedUser}
									loading={loadingByToken || loading}
									handlers={{ setProfileCompletionModal }}
								/>
							</MainCard>

							<Card autoHeight noHover>
								<Section noPadding>
									<Row type="flex" justify="space-between" style={{ marginBottom: "1.5em" }}>
										<Col lg={8}>
											<Heading content={<Trans>Testimonials</Trans>} level={4} />
										</Col>
										{token && testimonials.length > 3 && (
											<Col lg={8} style={{ textAlign: "right" }}>
												<ButtonLink>
													<Trans>
														See more <Icon type="right" />
													</Trans>
												</ButtonLink>
											</Col>
										)}
									</Row>
									{loadingByToken || loading ? (
										<Loading />
									) : (
										<Row>
											<Col lg={24} style={{ position: "relative" }}>
												<RenderTestimonials
													unauthorizedUser={unauthorizedUser}
													tutorTestimonials={tutorTestimonials}
													testimonials={testimonials}
												/>
											</Col>
										</Row>
									)}
								</Section>
							</Card>

							<Row gutter={32} type="flex" className="mb2em">
								<Col lg={12} xs={24} className="mb2em-mobile">
									<MainCard
										title={
											<CardTitle
												id="aboutme"
												title={<Trans>About me</Trans>}
												onEditMode={handleEditMode}
												unauthorizedUser={unauthorizedUser}
											/>
										}
									>
										<AboutMe
											data={userBio}
											userData={userData}
											onUserBio={{ userBio, setUserBio }}
											fetchUser={fetchUser}
											onEditMode={{ editMode, setEditMode }}
											unauthorizedUser={unauthorizedUser}
											loading={loadingByToken || loading}
										/>
									</MainCard>
								</Col>
								<Col lg={12} xs={24}>
									<MainCard
										title={
											<CardTitle
												id="education"
												title={<Trans>Education</Trans>}
												onEditMode={handleEditMode}
												unauthorizedUser={unauthorizedUser}
											/>
										}
									>
										<Education
											data={userBio}
											userData={userData}
											onEducation={{ education, setEducation }}
											fetchUser={fetchUser}
											onEditMode={{ editMode, setEditMode }}
											unauthorizedUser={unauthorizedUser}
											loading={loadingByToken || loading}
										/>
									</MainCard>
								</Col>
							</Row>

							<MainCard
								title={
									<CardTitle
										id="experiences"
										title={<Trans>Tutoring experiences</Trans>}
										description={experiencesDescription}
										onEditMode={handleEditMode}
										unauthorizedUser={unauthorizedUser}
									/>
								}
							>
								<Experiences
									onExperiences={{ experiences, setExperiences, theTotal, experiencesNonAcademic }}
									onEditMode={{ editMode, setEditMode }}
									unauthorizedUser={unauthorizedUser}
									fetchUser={fetchUser}
									userData={userData}
									loading={loadingByToken || loading}
								/>
							</MainCard>

							<Row type="flex" gutter={32} className="mb2em">
								<Col xs={24} lg={12} style={{ marginBottom: mobile && "1em" }}>
									<MainCard
										title={
											<CardTitle
												id="subjects"
												title={<Trans>Subjects</Trans>}
												onEditMode={handleEditMode}
												unauthorizedUser={unauthorizedUser}
											/>
										}
										editMode={editMode}
									>
										<Subjects
											onSubjects={{ subjects, setSubjects, nonAcademicSubjects }}
											onEditMode={{ editMode, setEditMode }}
											fetchUser={fetchUser}
											userData={userData}
											unauthorizedUser={unauthorizedUser}
											loading={loadingByToken || loading}
										/>
									</MainCard>
								</Col>

								<Col xs={24} lg={12}>
									<MainCard
										editMode={editMode}
										title={
											<CardTitle
												id="certifications"
												title={<Trans>Certifications</Trans>}
												onEditMode={handleEditMode}
												unauthorizedUser={unauthorizedUser}
											/>
										}
									>
										<Certifications
											onEditMode={{ editMode, setEditMode }}
											onCertifications={{ certifications, setCertifications }}
											unauthorizedUser={unauthorizedUser}
											fetchUser={fetchUser}
											loading={loadingByToken || loading}
										/>
									</MainCard>
								</Col>
							</Row>
							{booking && booking.getBookings.length > 0 && (
								<MainCard
									title={
										<CardTitle
											noEdit
											id="booking-pending"
											title={<Trans>Booking Pending</Trans>}
											onEditMode={handleEditMode}
											unauthorizedUser={unauthorizedUser}
										/>
									}
								>
									<List
										dataSource={booking.getBookings}
										itemLayout="horizontal"
										renderItem={(item) => (
											<List.Item>
												<List.Item.Meta
													title={`${item.subjectInfo.subject} - ${item.subjectInfo.level}`}
													description={
														<>
															<Heading
																level={4}
																marginBottom="2em"
																subheader={
																	<>
																		<Typography.Paragraph>
																			<Icon type="user" style={{ marginRight: "0.5rem" }} />
																			{`${item.student.firstName} ${item.student.lastName}  `}
																			<Icon type="mail" style={{ margin: "0 0.5rem" }} />
																			{`${item.student.email}`}
																		</Typography.Paragraph>
																		<Typography.Paragraph>
																			<Icon type="clock-circle" style={{ marginRight: "0.5rem" }} />
																			{`${moment(item.startTime).format(
																				"dddd, DD MMM YYYY hh:mm A"
																			)} - ${moment(item.endTime).format(
																				"dddd, DD MMM YYYY hh:mm A"
																			)}  `}
																			<Icon type="pushpin" style={{ margin: "0 0.5rem" }} />
																			{`${item.location}`}
																		</Typography.Paragraph>
																	</>
																}
															/>
														</>
													}
												/>
												<Tooltip title={<Trans>Accept this booking</Trans>}>
													<Button
														type="primary"
														icon="check-circle"
														onClick={() => handleUpdateBooking(item, "accept")}
														style={{ margin: "0 1em" }}
													>
														&nbsp; <Trans>Accept</Trans>
													</Button>
												</Tooltip>
												<Tooltip title={<Trans>Reject this booking</Trans>}>
													<Button
														type="error"
														icon="stop"
														onClick={() => handleUpdateBooking(item, "reject")}
														style={{ margin: "0 1em" }}
													>
														&nbsp; <Trans>Reject</Trans>
													</Button>
												</Tooltip>
											</List.Item>
										)}
									/>
								</MainCard>
							)}
							<MainCard
								title={
									<CardTitle
										noEdit
										id="schedule"
										title={<Trans>Schedule</Trans>}
										onEditMode={handleEditMode}
										unauthorizedUser={unauthorizedUser}
									/>
								}
								description={<Trans>Click to make a booking</Trans>}
							>
								<TutorCalendar
									onEditMode={{ editMode, setEditMode }}
									unauthorizedUser={unauthorizedUser}
									teachingDate={teachingDate}
									refetchData={handleUserStatus}
									loading={loadingByToken || loading}
									userData={userData}
								/>
							</MainCard>
						</Col>
					</Row>
				</Section>
			</Suspense>
		</Layout>
	)
}

export default withRouter(withI18n()(TutorProfile))

